import {
	mapState
} from 'vuex';
export default {
	data() {
		return {
			source: 0,
			addressList: [],
			modalName2: null,
			listTouchStart: 0,
		}
	},
	computed: {
		...mapState(['token'])
	},
	onLoad(option) {
		this.source = option.source;
	},
	onShow() {
		this.addressList = []
		this.getUserAddress();
	},
	methods: {
		setDefault(item) {
			var that = this;
			var now_token = that.token
			uni.request({
				url: that.global_baseUrl + '/set_default/' + item.id + "/",
				header: {
					'Authorization': ' Token ' + now_token
				},
				method: "GET",
				success: (res) => {
					that.check_statusCode(res.statusCode)
					that.addressList = [];
					that.getUserAddress();
				}
			});
		},
		// 删除地址
		deleteAddress(item) {
			var that = this;
			uni.showModal({
				title: '提示',
				content: '确定删除该地址吗？',
				success: function(res) {
					if (res.confirm) {
						var now_token = that.token
						uni.request({
							url: that.global_baseUrl + '/user_address_detail/' + item.id + "/",
							header: {
								'Authorization': ' Token ' + now_token
							},
							method: "DELETE",
							success: (res) => {
								that.check_statusCode(res.statusCode)
								that.addressList = [];
								that.getUserAddress();
							}
						});
					} else if (res.cancel) {

					}
				}
			});
		},
		getUserAddress() {
			this.addressList = [];
			var now_token = this.token
			uni.request({
				url: this.global_baseUrl + '/user_address/',
				header: {
					'Authorization': ' Token ' + now_token
				},
				method: "GET",
				success: (res) => {
					this.check_statusCode(res.statusCode);
					for (var i = 0; i < res.data.length; i++) {
						var temp_address = {
							id: res.data[i].id,
							name: res.data[i].consignee_name,
							mobile: res.data[i].phone_num,
							address: res.data[i].consinfee_address,
							default: i == 0 ? true : false,
							area: res.data[i].note,
							belong_user: res.data[i].belong_user
						}
						this.addressList.push(temp_address);
					}
				},

			});
		},
		//选择地址
		checkAddress(item) {
			if (this.source == 1) {
				this.$api.prePage().addressData = item;
				uni.navigateBack()
			}
		},
		addAddress(type, item) {
			uni.navigateTo({
				url: `/pages/address/addressManage?type=${type}&data=${JSON.stringify(item)}`
			})
		},
		// ListTouch触摸开始
		ListTouchStart2(e) {
			this.listTouchStart2 = e.touches[0].pageX

		},
		// ListTouch计算方向
		ListTouchMove2(e) {
			this.listTouchDirection2 = e.touches[0].pageX - this.listTouchStart2 > 0 ? 'right' : 'left';
		},

		// ListTouch计算滚动
		ListTouchEnd2(e) {
			if (this.listTouchDirection2 == 'left') {
				this.modalName2 = e.currentTarget.dataset.target
			} else {
				this.modalName2 = null
			}
			this.listTouchDirection2 = null
		},
		//添加或修改成功之后回调
		// refreshList(data, type){
		//添加或修改后事件，这里直接在最前面添加了一条数据，实际应用中直接刷新地址列表即可
		// this.addressList.unshift(data);
		// }
	}
}
